<template>
 <div>
  父组件
  <img v-for="item in src" :src="item" alt="loading"> 
<div style="height:800px;width:100%">
  <img v-lazy data-src="../../static/a.jpg" alt="loading" >
</div>
  <div style="height:800px;width:100%">
   <img v-lazy data-src="../../static/b.jpg" alt="loading" >
   </div>
    <div style="height:800px;width:100%">
   <img v-lazy data-src="../../static/c.jpg" alt="loading" >
   </div>
  <!-- <img v-lazy='{src:"../../static/a.jpg"}' alt="loading"> -->
  <child :child-data="asyncData" :loadimg='loadimg'></child>
 </div>
</template>

<script>

import child from "@/components/Children";
export default {
  name: "Father",
  data() {
    return {
      asyncData: false,
      src: []
    };
  },
  components: {
    child
  },
  created() {},
  mounted() {
    // setTimeout模拟异步数据
    setTimeout(() => {
      this.asyncData = true;
      console.log("parent finish");
    }, 2000);

    const map = new Map([["F", "no"], ["T", "yes"]]);
    console.log(Array.isArray(map.keys()));
    for (let key of map.keys()) {
      console.log(key);
    }
  },
  methods: {
    loadimg() {
      let that = this;
      Promise.all([
        import("../assets/a.jpg"),
        import("../assets/b.jpg")
      ]).then(function(detail) {
        console.log(detail);
        that.src = detail;
      });
    }
  },
  // directives: {
  //   lazy: {
  //     inserted(el, bilding) {
  //       let windowHeight = window.innerHeight;
  //       let isInview = window.innerHeight > el.getBoundingClientRect().top;
      
  //       window.addEventListener("scroll", function(event) {
  //          if (!el.src && isInview) {
  //         el.src = el.dataset.src;
  //       }
  //       });
  //     }
  //   }
  // }
};
</script>


